h1, h2, h3 {
  font-family: "Inter", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
}

h1 {
  font-size: 6rem;
}

h2 {
  font-size: 5rem;
}

h3 {
  font-size: 4rem;
}

p {
  font-size: 2rem;
  color: #6b7280;
  line-height: 1.8;
}

.descripcion-pagina {
  text-align: center;
  margin: 0;
}

.tagline {
  text-align: center;
  font-weight: 900;
  font-size: 3rem;
  margin: 2rem 0;
  color: #262626;
  font-family: "Inter", sans-serif;
}

.contenido-pagina {
  text-align: center;
}

/** Media Queries **/
*, *:before, *:after {
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  min-height: 100vh;
}

.contenedor {
  width: 90%;
  max-width: 1200px;
  margin: 0 auto;
}

.contenedor-sm {
  max-width: 70rem;
  margin: 0 auto;
  padding: 1rem;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

.formulario {
  padding: 2rem;
}
.formulario .campo {
  display: flex;
  flex-direction: column;
  padding: 1rem;
  justify-content: center;
}
@media (min-width: 768px) {
  .formulario .campo {
    flex-direction: row;
    align-items: center;
  }
}
.formulario .campo label {
  display: block;
  margin-bottom: 2rem;
}
