.formulario .campo input {
  flex: 1;
  border: none;
  background-color: #d8dadf;
  padding: 1rem;
}

.acciones {
  margin: 3rem 0;
}
@media (min-width: 768px) {
  .acciones {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.acciones a {
  display: block;
  margin-bottom: 2rem;
  text-align: center;
  color: #000;
  font-weight: 700;
}
.acciones a:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .acciones a {
    margin-bottom: 0;
  }
}

.alerta {
  font-size: 1.6rem;
  font-weight: bold;
  margin: 1rem auto;
  padding: 0.5rem 2rem;
  max-width: 90%;
}
