  background-color: #fac3ce;
  color: crimson;
  border-left: 0.3rem solid crimson;
}
.alerta.exito {
  background-color: #92ff92;
  color: rgb(0, 120, 0);
  border-left: 0.3rem solid rgb(0, 120, 0);
}

