.sidebar {
  opacity: 0;
  height: 0;
  overflow: hidden;
  transition-property: opacity;
  transition-duration: 250ms;
}
.sidebar.show {
  opacity: 1;
  height: 100%;
  padding: 2rem 4rem;
  background-color: #4338ca;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.sidebar.hide {
  opacity: 0;
}
@media (min-width: 768px) {
  .sidebar {
    opacity: 1;
    height: 100%;
    background-color: #4338ca;
    padding: 1rem;
  }
}
.sidebar .sidebar-mobile {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.sidebar .sidebar-mobile img {
  width: 2rem;
}
@media (min-width: 768px) {
  .sidebar .sidebar-mobile img {
    display: none;
  }
}
