.modal {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.5);
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.modal .formulario {
  max-width: 60rem;
  background-color: #FFFFFF;
  padding: 2rem;
  margin: 0 auto;
  border-radius: 1rem;
  position: relative;
  transform: translateY(-30rem);
  transition-property: all;
  transition-duration: 300ms;
  transition-timing-function: ease-out;
  opacity: 0;
}
.modal .formulario legend {
  margin: 0 auto 3rem auto;
  width: 100%;
  text-align: center;
  font-weight: bold;
  font-size: 2.4rem;
  color: #4B5563;
}
.modal .formulario label {
  color: #4B5563;
}
.modal .formulario input[type=text] {
  background-color: #f3f4f6;
  border-radius: 0.5rem;
}
.modal .formulario.animar {
  top: 50%;
  transform: translateY(-50%);
  opacity: 1;
}
.modal .formulario.cerrar {
  top: 0;
  transform: translateY(-30rem);
  opacity: 0;
}
.modal .opciones {
  margin-top: 2rem;
  display: flex;
}
@media (min-width: 768px) {
  .modal .opciones {
    justify-content: space-between;
  }
}
.modal .opciones .btn-crear-tarea {
  background-color: #4338ca;
  color: #FFFFFF;
  text-align: center;
  font-weight: 700;
  padding: 1rem 3rem;
  margin-top: 2rem;
  display: block;
  border: none;
  cursor: pointer;
}
@media (min-width: 768px) {
  .modal .opciones .btn-crear-tarea {
    width: auto;
  }
}
.modal .opciones .btn-crear-tarea:hover {
  background-color: #382fb0;
}
.modal .opciones .btn-cerrar-modal {
  background-color: #F59E0B;
  color: #FFFFFF;
  text-align: center;
  font-weight: 700;
  padding: 1rem 3rem;
  margin-top: 2rem;
  display: block;
  border: none;
  cursor: pointer;
}
@media (min-width: 768px) {
  .modal .opciones .btn-cerrar-modal {
    width: auto;
  }
}
.modal .opciones .btn-cerrar-modal:hover {
  background-color: #d48809;
}
.modal .opciones .btn-cerrar-modal,
.modal .opciones .btn-crear-tarea {
  flex: 1 1 50%;
  padding: 2rem;
  font-size: 1.8rem;
}
@media (min-width: 768px) {
  .modal .opciones .btn-cerrar-modal,
.modal .opciones .btn-crear-tarea {
    flex: 0;
    font-size: 1.6rem;
    padding: 1rem 3rem;
  }
}

.filtros {
  margin-bottom: 4rem;
  padding: 3rem;
  border-radius: 1rem;
  background-color: #FFFFFF;
}
.filtros .filtros-inputs h2 {
  font-size: 2.8rem;
  color: #4338ca;
}
@media (min-width: 768px) {
  .filtros .filtros-inputs h2 {
    margin: 0;
  }
}
@media (min-width: 768px) {
  .filtros .filtros-inputs {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 2rem;
    margin: 0;
  }
}
.filtros .filtros-inputs .campo {
  margin-bottom: 2rem;
  display: flex;
  justify-content: space-between;
  gap: 2rem;
}
@media (min-width: 768px) {
  .filtros .filtros-inputs .campo {
    margin-bottom: 0;
  }
}
.filtros .filtros-inputs input[type=radio] {
  accent-color: #4338ca;
  height: 2rem;
  width: 2rem;
}

@media (prefers-color-scheme: dark) {
  body {
    background-color: #2c2c3b;
  }
  .tagline,
.descripcion-pagina,
.campo label,
.formulario .campo input,
.contenido .nombre-pagina,
.filtros .filtros-inputs h2 {
    color: #f0f1f4;
  }
  .login .acciones a {
    color: #5bd9f8;
  }
  .crear .acciones a {
    color: #ba97f6;
  }
  .recuperar .acciones a {
    color: #e25393;
  }
  .confirmar .acciones a,
.dashboard .enlace {
    color: #8f89df;
  }
  .formulario .campo input,
.dashboard .formulario input:not([type=submit]) {
    background-color: #d8dadf;
    color: #000;
  }
  .formulario .campo input::placeholder,
.dashboard .formulario input:not([type=submit])::placeholder {
    color: #4B5563;
  }
  .sidebar,
.sidebar.show,
.barra-mobile {
    background-color: #16161e;
  }
  .sidebar .sidebar-nav a.activo,
.sidebar .sidebar-nav a:hover {
    background-color: #F59E0B;
  }
  .barra {
    background-color: #0b0b0f;
  }
  .barra P {
    color: #F59E0B;
  }
  .barra P span {
    color: #e4e7eb;
  }
  .barra .cerrar-sesion {
    background-color: #F59E0B;
    color: #FFFFFF;
    text-align: center;
    font-weight: 700;
    padding: 1rem 3rem;
    margin-top: 2rem;
    display: block;
    border: none;
    cursor: pointer;
    margin: 0;
    padding: 1rem;
    border-radius: 0.7rem;
    font-size: 1.2rem;
  }
}
@media (prefers-color-scheme: dark) and (min-width: 768px) {
  .barra .cerrar-sesion {
    width: auto;
  }
}
@media (prefers-color-scheme: dark) {
  .barra .cerrar-sesion:hover {
    background-color: #d48809;
  }
}
@media (prefers-color-scheme: dark) {
  .filtros {
    background-color: #626284;
  }
  .filtros .filtros-inputs input[type=radio] {
    accent-color: #000;
  }
}
@media (prefers-color-scheme: dark) {
  .principal {
    background-color: #2c2c3b;
  }
}
@media (prefers-color-scheme: dark) {
  .lista-tareas .tarea p {
    color: #e4e7eb;
  }
  .lista-tareas .tarea p:hover {
    color: #b9bfca;
  }
}
@media (prefers-color-scheme: dark) {
  .lista-proyectos .proyecto {
    background-color: #585875;
  }
  .lista-proyectos .proyecto:hover {
    background-color: #424258;
  }
}
@media (prefers-color-scheme: dark) {
  .modal .formulario {
    background-color: #424258;
  }
  .modal .formulario legend,
.modal .formulario label {
    color: white;
  }
  .modal .formulario input[type=text] {
    background-color: #21212c;
    color: #FFFFFF;
  }
  .modal .formulario input[type=text]::placeholder {
    color: #858c99;
  }
}